import React from 'react';

//style
import styles from './Banner.module.css';

const Banner = () => {
    return(
        
            <div className={styles.main}>
                <div className={styles.contextMain}>
                    <h1>The smart way to crypto</h1>
                    <p>Clanz is a new Australian company demystifying crypto and the blockchain through education and easy to use products and investment strategies.</p>
                    <div className={styles.marketPics}>
                        <a href='https://apps.apple.com/au/app/clanz/id1616515398?platform=iphone' target="_blank" rel="noreferrer"><img className={styles.googlePlayPic} src="https://uploads-ssl.webflow.com/628ac869149177455079e47d/62989af75a6e94cf34dab79e_AppleAppStore_Light.svg" alt="appStorePic"/></a>
                        <a href='https://play.google.com/store/apps/details?id=com.clanz' target="_blank" rel='noreferrer'><img className={styles.appStorePic} src='https://uploads-ssl.webflow.com/628ac869149177455079e47d/62989af6c6c39d7d747dec65_GooglePlay_Light.svg' alt="googlePlayPic"/></a>
                    </div>
                </div>
                <img className={styles.mobilePic} src='https://uploads-ssl.webflow.com/628ac869149177455079e47d/62b399ece7a68d1c2a34a83d_product_app_hero-p-500.png' alt='mobilePic'/>
            </div>
    
    )
}

export default Banner;