import React from 'react';

//style
import styles from './Loader.module.css'

// Gif
import Loading from '../gif/Loading.svg'

const Loader = () => {
    return (
        <div className={styles.loaderContainer}>
            <img src={Loading} alt="Loading" />
            <h1>Loading...</h1>
        </div>
    );
};

export default Loader;